"use client"

import { useEffect, useState, useRef } from "react"
import { usePathname, useRouter } from "next/navigation"
import Link from "next/link"
import { SidebarProvider, SidebarTrigger } from "@/components/ui/sidebar"
import { useLanguage, useT } from "@/components/language-provider"
import { onAuthStateChanged, signOut } from "firebase/auth"
import { doc, getDoc } from "firebase/firestore"
import { auth, db, isMockMode } from "@/firebase/firebaseConfig"
import { Loader2, LayoutDashboard, ShoppingCart, User, Settings, LogOut, ClipboardList } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"

export default function CustomerLayout({ children }: { children: React.ReactNode }) {
    const { lang, setLang } = useLanguage()
    const t = useT()
    const isAr = lang === "ar"
    const pathname = usePathname()
    const router = useRouter()
    const [loading, setLoading] = useState(true)
    const [user, setUser] = useState<any>(null)
    const navigatingRef = useRef(false)

    useEffect(() => {
        if (isMockMode) {
            const mockUser = localStorage.getItem("mock_customer_user")
            if (mockUser) {
                setUser(JSON.parse(mockUser))
                if (pathname === "/customer/login" && !navigatingRef.current) {
                    navigatingRef.current = true
                    router.replace("/customer")
                }
            } else {
                setUser(null)
                if (pathname !== "/customer/login" && !navigatingRef.current) {
                    navigatingRef.current = true
                    router.replace("/customer/login")
                }
            }
            setLoading(false)
            return
        }

        const unsubscribe = onAuthStateChanged(auth, async (currentUser) => {
            if (currentUser) {
                // Verify user has customer role
                const userDoc = await getDoc(doc(db, "users", currentUser.uid))
                if (userDoc.exists() && userDoc.data().role === "customer") {
                    setUser({ ...currentUser, ...userDoc.data() })
                    if (pathname === "/customer/login" && !navigatingRef.current) {
                        navigatingRef.current = true
                        router.replace("/customer")
                    }
                } else {
                    // Not a customer, redirect to login
                    await signOut(auth)
                    setUser(null)
                    if (pathname !== "/customer/login" && !navigatingRef.current) {
                        navigatingRef.current = true
                        router.replace("/customer/login")
                    }
                }
            } else {
                setUser(null)
                if (pathname !== "/customer/login" && !navigatingRef.current) {
                    navigatingRef.current = true
                    router.replace("/customer/login")
                }
            }
            setLoading(false)
        })

        return () => unsubscribe()
    }, [pathname, router])

    const handleLogout = async () => {
        if (isMockMode) {
            localStorage.removeItem("mock_customer_user")
        } else {
            await signOut(auth)
        }
        router.push("/customer/login")
    }

    if (loading) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center text-white">
                <Loader2 className="w-8 h-8 animate-spin text-cyan-500" />
            </div>
        )
    }

    // If on login page, render without navigation
    if (pathname === "/customer/login") {
        return <div className={cn("min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900", isAr && "[direction:rtl]")}>{children}</div>
    }

    // If not authenticated and not on login page, return null
    if (!user && pathname !== "/customer/login") return null

    return (
        <div className={cn("min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 text-white", isAr && "[direction:rtl]")}>
            <div className="flex min-h-screen">
                {/* Sidebar for desktop */}
                <aside className="hidden lg:flex flex-col w-64 border-r border-white/10 bg-black/20 backdrop-blur-xl">
                    <div className="p-6 border-b border-white/10">
                        <h1 className="text-2xl font-bold bg-gradient-to-r from-cyan-400 to-blue-500 bg-clip-text text-transparent">
                            KBI Portal
                        </h1>
                        <p className="text-sm text-white/60 mt-1">Customer Dashboard</p>
                    </div>

                    <nav className="flex-1 p-4 space-y-2">
                        {[
                            { href: "/customer", label: t("Dashboard"), icon: LayoutDashboard },
                            { href: "/customer/orders", label: t("My Orders"), icon: ShoppingCart },
                            { href: "/customer/track", label: t("Track Repair"), icon: ClipboardList },
                            { href: "/customer/profile", label: t("Profile"), icon: User },
                        ].map((item) => {
                            const active = item.href === "/customer" ? pathname === "/customer" : pathname.startsWith(item.href)
                            const Icon = item.icon
                            return (
                                <Link
                                    key={item.href}
                                    href={item.href}
                                    prefetch={false}
                                    className={cn(
                                        "flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200",
                                        active
                                            ? "bg-gradient-to-r from-cyan-500/20 to-blue-500/20 text-cyan-400 border border-cyan-500/30"
                                            : "text-white/70 hover:bg-white/5 hover:text-white"
                                    )}
                                >
                                    <Icon className="w-5 h-5" />
                                    <span className="font-medium">{item.label}</span>
                                </Link>
                            )
                        })}
                    </nav>

                    <div className="p-4 border-t border-white/10">
                        <div className="flex items-center gap-3 mb-4 px-2">
                            <div className="w-10 h-10 rounded-full bg-gradient-to-br from-cyan-500 to-blue-600 flex items-center justify-center font-bold">
                                {user?.name?.charAt(0) || "C"}
                            </div>
                            <div>
                                <p className="text-sm font-medium">{user?.name || "Customer"}</p>
                                <p className="text-xs text-white/50">{user?.email}</p>
                            </div>
                        </div>
                        <Button
                            onClick={handleLogout}
                            variant="ghost"
                            className="w-full justify-start gap-3 text-white/70 hover:text-red-400 hover:bg-red-500/10"
                        >
                            <LogOut className="w-5 h-5" />
                            {t("Log Out")}
                        </Button>
                    </div>
                </aside>

                {/* Main content */}
                <main className="flex-1 flex flex-col min-w-0 overflow-hidden">
                    <header className="flex items-center gap-4 border-b border-white/10 bg-black/30 px-6 py-4 backdrop-blur-md sticky top-0 z-10">
                        <div className="flex-1">
                            <h2 className="text-lg font-semibold text-white">{t("Welcome")}, {user?.name || "Customer"}</h2>
                        </div>
                        <div className="flex items-center gap-2">
                            <button
                                onClick={() => setLang("en")}
                                className={cn("px-3 py-1.5 rounded-full text-xs font-semibold border border-white/10 transition-colors", lang === "en" ? "bg-cyan-500 text-black border-cyan-500" : "text-white/70 hover:bg-white/10")}
                            >
                                EN
                            </button>
                            <button
                                onClick={() => setLang("ar")}
                                className={cn("px-3 py-1.5 rounded-full text-xs font-semibold border border-white/10 transition-colors", lang === "ar" ? "bg-cyan-500 text-black border-cyan-500" : "text-white/70 hover:bg-white/10")}
                            >
                                AR
                            </button>
                        </div>
                    </header>

                    <div className="flex-1 overflow-auto p-6 pb-24 lg:pb-6">
                        {children}
                    </div>

                    {/* Mobile bottom navigation */}
                    <nav className="lg:hidden fixed inset-x-0 bottom-0 z-20 border-t border-white/10 bg-black/80 backdrop-blur-xl">
                        <div className="flex items-stretch justify-around">
                            {[
                                { href: "/customer", label: t("Home"), icon: LayoutDashboard },
                                { href: "/customer/orders", label: t("Orders"), icon: ShoppingCart },
                                { href: "/customer/track", label: t("Track"), icon: ClipboardList },
                                { href: "/customer/profile", label: t("Profile"), icon: User },
                            ].map((item) => {
                                const active = item.href === "/customer" ? pathname === "/customer" : pathname.startsWith(item.href)
                                const Icon = item.icon
                                return (
                                    <Link
                                        key={item.href}
                                        href={item.href}
                                        prefetch={false}
                                        className={cn("flex-1 py-3 flex flex-col items-center justify-center gap-1", active ? "text-cyan-400" : "text-white/50")}
                                    >
                                        <Icon className="w-5 h-5" />
                                        <span className="text-[10px] font-medium">{item.label}</span>
                                    </Link>
                                )
                            })}
                        </div>
                    </nav>
                </main>
            </div>
        </div>
    )
}
